import { VE_ActionBehaviour, VE_Actions } from "../../action";
import { ParaParserUtility, VeryParas, ErrorInfo } from "../../utility";
import { ParaType } from "../../enum";
import { VeryNumber, VeryString, IVeryVar, VeryBool, VarTools, VeryVector3 } from "../../variables";

export class Action_Set_Light_Color extends VE_ActionBehaviour {

  public get ID(): string {
    return "Set_Light_Color|设置灯光颜色";
  }

  public get className(): string {
    return "Action_Set_Light_Color";
  }

  private _light: BABYLON.Nullable<BABYLON.Light> = null;

  private _isColor: boolean = false;
  private _blueColor: VeryVector3 = new VeryVector3();
  private _colorFloat: VeryNumber[] = [];

  public active(): void {
    if (this._light) {
      if (!this._isColor) {
        this._light.diffuse = new BABYLON.Color3(this._colorFloat[0].value / 255, this._colorFloat[1].value / 255, this._colorFloat[2].value / 255);
      }
      else {
        this._light.diffuse = new BABYLON.Color3(this._blueColor.value.x / 255, this._blueColor.value.y / 255, this._blueColor.value.z / 255);
      }
    }
  }

  public onUpdate(): void {

  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2)) {
      return false;
    } else {

      // 参数1：
      this._light = Light_Tools.GetLight(this.scene, para_array[0], this.ID);

      //参数2
      let para3: VeryParas = new VeryParas();
      if (!ParaParserUtility.Vector3OrNumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Trigger, para3, para_array[1])) {
        return false;
      }
      this._isColor = para3.isVec;
      this._blueColor = para3.vector3;
      this._colorFloat = para3.vecNumbers;

      return true;
    }
  }
  public destroy(): void {

  }
}

