export class Action_Set_Light_Spot_Angle extends VE_ActionBehaviour {

  public get ID(): string {
    return "Set_Light_Spot_Angle|设置灯光聚光角度";
  }

  public get className(): string {
    return "Action_Set_Light_Spot_Angle";
  }

  private _light: BABYLON.Nullable<BABYLON.SpotLight> = null;
  private _angle: VeryNumber = new VeryNumber();

  public active(): void {
    if (this._light) {
      this._light.angle = this._angle.value;
    }
  }

  public onUpdate(): void {

  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2)) {
      return false;
    } else {

      // 参数1：
      var tmpLight = Light_Tools.GetLight(this.scene, para_array[0], this.ID);
      if (tmpLight?.getClassName() != "SpotLight") {
        console.error(this.ID + " 响应传入参数格式错误： " + para_array[0] + "，该物体不是SpotLight类型！");
        return false;
      }

      //参数2
      let para3: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberPara(this.projectName, this.objectID, this.ID, ParaType.Trigger, para3, para_array[1])) {
        return false;
      }
      this._angle = para3.number;

      return true;
    }
  }
  public destroy(): void {

  }
}

//TODO:20200805
VE_Actions.addAction(Action_Set_Light_Color);
VE_Actions.addAction(Action_Set_Light_Intensity);
VE_Actions.addAction(Action_Set_Light_Range);
VE_Actions.addAction(Action_Set_Light_Spot_Angle);


export class Light_Tools {
  /*
    获取UI
  */
  public static GetLight(scene: BABYLON.Scene, name: string, id: string): BABYLON.Nullable<BABYLON.Light> {
    for (var i = 0; i < scene.lights.length; i++) {
      if (name == scene.lights[i].name) {
        return scene.lights[i];
      }
    }
    console.error(id + "--响应参数出错--" + name);
    return null;
  }
}