export class Action_UGUI_Set_SizeDelta extends VE_ActionBehaviour {

  public get ID(): string {
    return "GUI_Set_SizeDelta|设置GUI大小";
  }

  public get className(): string {
    return "Action_UGUI_Set_SizeDelta";
  }

  private _control: BABYLON.GUI.Control = new BABYLON.GUI.Control();

  private _posValue: VeryNumber = new VeryNumber();
  private _isVec = false;
  private _posVec: VeryVector2 = new VeryVector2();
  private _posVecFloat: VeryNumber[] = [];

  private _axis = 0;

  public active(): void {

  }

  public onUpdate(): void {
    if (this._axis == 0) {
      this._control.width = this._posValue.value + "px";
    }
    else if (this._axis == 1) {
      this._control.height = this._posValue.value + "px";
    }
    else {
      if (!this._isVec) {
        this._control.width = this._posVecFloat[0].value + "px";
        this._control.height = this._posVecFloat[1].value + "px";

      }
      else {
        this._control.width = this._posVec.value.x + "px";
        this._control.height = this._posVec.value.y + "px";
      }
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 3)) {
      return false;
    } else {
      //第一个参数
      var tmpControl = UGUI_Tools.GetUI(this.scene, para_array[0], this.ID);
      if (tmpControl == null) {
        console.error(this.ID + "--响应参数出错,UI不存在--" + para_array[0]);
      }
      else {
        this._control = tmpControl;
      }

      // 参数2：
      if (para_array[1].toLowerCase() == "x") {
        this._axis = 0;
      }
      else if (para_array[1].toLowerCase() == "y") {
        this._axis = 1;
      }
      else if (para_array[1].toLowerCase() == "vector2") {
        this._axis = 2;
      }
      else {
        console.error(this.ID + " 响应参数格式错误，轴参数应为x、y、z或者vector2，当前为：" + para_array[1] + "，请检查！");
        return false;
      }

      //参数3
      let para2: VeryParas = new VeryParas();
      if (this._axis == 0 || this._axis == 1) {
        if (!ParaParserUtility.NumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[2])) {
          return false;
        }
        this._posValue = para2.number;
      }
      else {
        if (!ParaParserUtility.Vector2OrNumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[2])) {
          return false;
        }
        this._isVec = para2.isVec2;
        this._posVec = para2.vector2;
        this._posVecFloat = para2.vec2Numbers;
      }

      return true;
    }
  }
  public destroy(): void {

  }
}

