import { VE_ActionBehaviour, VE_Actions } from "../../action";
import { ParaParserUtility, VE_KeyboardMap, VeryParas } from '../../utility';
import { ParaType, Severity } from '../../enum';
import { VE_ErrorManager, VE_Error } from '../../global';
import { VeryGameObject, VeryBool, VeryNumber, VeryString, VeryRect, VeryVector3, VeryVector2, VeryMaterial } from "../../variables";

export class Action_VedioPlayOnOBJ extends VE_ActionBehaviour {

  public get ID(): string {
    return 'VedioPlayOnOBJ|物体上视频播放';
  }

  public get className(): string {
    return 'Action_VedioPlayOnOBJ';
  }

  //是否开启
  private _switch: VeryBool = new VeryBool();
  //物体
  private _blueOBJ: VeryGameObject = new VeryGameObject();
  //播放视频
  private _musicString: VeryString = new VeryString();
  //视频循环
  private _musicLoop: VeryBool = new VeryBool();
  //视频音乐大小
  private _musicVolume: VeryNumber = new VeryNumber();

  //视频
  private _vedio: BABYLON.Nullable<BABYLON.VideoTexture> = null;
  private _currentMesh: BABYLON.Nullable<BABYLON.AbstractMesh> = null;
  //物体原有材质
  private _oldMat: BABYLON.Nullable<BABYLON.Material> = null;
  private _newMat: BABYLON.Nullable<BABYLON.StandardMaterial> = null;

  public active(): void {
    //console.log("Vedio--" + this._vedio?.isReady() + "--" + this._vedio?.video.readyState);
    if (this._vedio != null && this._currentMesh) {
      if (this._switch.value) {
        //物体材质
        if (this._newMat == null) {
          console.log("Vedio--" + this._vedio.video.videoWidth + "--" + this._vedio.video.videoHeight);
          this._newMat = new BABYLON.StandardMaterial("m", this.scene);
          this._newMat.diffuseTexture = this._vedio;
          //this._newMat.roughness = 1;
          //this._newMat.emissiveColor = BABYLON.Color3.White();
        }
        this._vedio.video.loop = this._musicLoop.value;
        this._vedio.video.volume = (this._musicVolume.value);
        //物体材质
        this._currentMesh.material = this._newMat;

        this._vedio.video.play();
      }
      else {
        this._vedio.video.loop = this._musicLoop.value;
        this._vedio.video.volume = (this._musicVolume.value);
        this._vedio.video.pause();

        //物体材质
        this._currentMesh.material = this._oldMat;
      }
    }
  }

  public onUpdate(): void {

  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Trigger, para_array.length, 5)) {
      return false;
    } else {
      // 第1个参数：
      let para0: VeryParas = new VeryParas();
      if (!ParaParserUtility.BoolParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[0])) {
        return false;
      }
      this._switch = para0.bool;

      // 第2个参数：
      let para5: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para5, para_array[1])) {
        return false;
      }
      this._blueOBJ = para5.gameObject;
      if (this._blueOBJ.value != null) {
        if (this._blueOBJ.value.mesh != null) {
          this._currentMesh = this._blueOBJ.value.mesh;
          this._oldMat = this._blueOBJ.value.mesh.material;
        }
        else {
          console.error("物体上视频播放--物体不存在Mesh--" + para_array[1]);
        }
      }
      else {
        console.error("物体上视频播放--物体不存在--" + para_array[1]);
      }

      //参数3
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[2])) {
        return false;
      }
      this._musicString = para1.string;
      this._vedio = new BABYLON.VideoTexture("Vedio", this._musicString.value, this.scene, true, true);
      this._vedio.video.autoplay = false;

      //参数4
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.BoolParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[3])) {
        return false;
      }
      this._musicLoop = para2.bool;

      //参数5
      let para3: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para3, para_array[4])) {
        return false;
      }
      this._musicVolume = para3.number;

      return true;
    }

  }
  public destroy(): void {

  }
}


