import { VE_ActionBehaviour, VE_Actions } from "../../action";
import { ParaParserUtility, VE_KeyboardMap, VeryParas } from '../../utility';
import { ParaType, Severity } from '../../enum';
import { VE_ErrorManager, VE_Error } from '../../global';
import { VeryGameObject, VeryBool, VeryNumber, VeryString, VeryRect, VeryVector3, VeryVector2 } from "../../variables";

export class Action_Get_Vertex_Count extends VE_ActionBehaviour {

  public get ID(): string {
    return 'Get_Vertex_Count|获取面片顶点数';
  }

  public get className(): string {
    return 'Action_Get_Vertex_Count';
  }


  private _blueGameobject: VeryGameObject = new VeryGameObject();
  private _blueInt: VeryNumber = new VeryNumber();

  public active(): void {

  }

  public onUpdate(): void {
    var tmpMesh = this._blueGameobject.value?.mesh;
    this._blueInt.value = 0;
    if (tmpMesh != null && tmpMesh != undefined) {
      //console.log(tmpMesh.name + "--" + tmpMesh.subMeshes.length + "--" + tmpMesh.getTotalVertices());
      this._blueInt.value = tmpMesh.getTotalVertices();
    }
  }


  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Trigger, para_array.length, 2)) {
      return false;
    } else {
      // 第1个参数：
      let para0: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[0])) {
        return false;
      }
      this._blueGameobject = para0.gameObject;
      var tmpMesh = this._blueGameobject.value?.mesh;
      if (tmpMesh == null || tmpMesh == undefined) {
        console.error(this.ID + " 响应传入参数格式错误： " + para_array[0] + "，该物体不具有Mesh！");
        return false;
      }

      //参数2
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[1])) {
        return false;
      }
      this._blueInt = para1.number;

      return true;
    }

  }

  public destroy(): void {

  }

}

export class Action_Get_Vertex_Position extends VE_ActionBehaviour {

  public get ID(): string {
    return 'Get_Vertex_Position|获取面片顶点坐标';
  }

  public get className(): string {
    return 'Action_Get_Vertex_Position';
  }


  private _blueGameobject: VeryGameObject = new VeryGameObject();
  private _blueInt: VeryNumber = new VeryNumber();
  private _blueVec3 = new VeryVector3();
  private _isWorldAxis = true;

  public active(): void {

  }

  public onUpdate(): void {
    var tmpMesh = this._blueGameobject.value?.mesh;
    if (tmpMesh != null && tmpMesh != undefined) {
      var positions = tmpMesh.getVerticesData("position");
      if (positions != null) {
        /*
        console.log("positions--" + positions.length);
        for (var i = 0; i < positions.length; i++) {
          console.log(i + "--positions--" + positions[i]);
        }
        */
        var tmpVec3 = new BABYLON.Vector3(positions[this._blueInt.value * 3], positions[this._blueInt.value * 3 + 1], positions[this._blueInt.value * 3 + 2]);
        if (this._isWorldAxis) {
          this._blueVec3.value = tmpMesh.getAbsolutePosition().add(tmpVec3);
        }
        else {
          this._blueVec3.value = tmpVec3;
        }
      }
    }
  }


  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Trigger, para_array.length, 4)) {
      return false;
    } else {
      // 第1个参数：
      let para0: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[0])) {
        return false;
      }
      this._blueGameobject = para0.gameObject;
      var tmpMesh = this._blueGameobject.value?.mesh;
      if (tmpMesh == null || tmpMesh == undefined) {
        console.error(this.ID + " 响应传入参数格式错误： " + para_array[0] + "，该物体不具有Mesh！");
        return false;
      }

      //参数2
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[1])) {
        return false;
      }
      this._blueInt = para1.number;
      //检测index是否out of index
      if (this._blueInt.value < 0 || this._blueInt.value > (tmpMesh.subMeshes[0].verticesCount - 1)) {
        console.error(this.ID + " 响应中，" + para_array[1] + "，该index超出了顶点索引范围，请查看填写是否正确！");
        return false;
      }

      //参数3
      if (para_array[2].toLowerCase() == "world" || para_array[2] == "世界") {
        this._isWorldAxis = true;
      }
      else {
        this._isWorldAxis = false;
      }

      //参数4
      let para3: VeryParas = new VeryParas();
      if (!ParaParserUtility.Vector3Para(this.projectName, this.objectID, this.ID, ParaType.Action, para3, para_array[3])) {
        return false;
      }
      this._blueVec3 = para3.vector3;

      return true;
    }

  }
  public destroy(): void {

  }
}

//TODO:20200807
VE_Actions.addAction(Action_Get_Vertex_Count);
VE_Actions.addAction(Action_Get_Vertex_Position);