import { VE_ActionBehaviour, VE_Actions } from "../../action";
import { ParaParserUtility, VeryParas, ErrorInfo } from "../../utility";
import { ParaType } from "../../enum";
import { VeryNumber, VeryString, IVeryVar, VeryBool, VarTools, VeryVector3, VeryGameObject, VeryVector2 } from "../../variables";
import { BabylonEngine } from "../../babylon";
import { Transform } from "src/veryengine/babylon";

export class Action_GetMousePosition_Screen extends VE_ActionBehaviour {

  public get ID(): string {
    return "GetMousePosition_Screen|获取鼠标屏幕位置";
  }

  public get className(): string {
    return "Action_GetMousePosition_Screen";
  }

  private _mousePosVe: VeryVector2 = new VeryVector2();
  private _mousePosX: VeryNumber = new VeryNumber();
  private _mousePosY: VeryNumber = new VeryNumber();

  public active(): void {

  }

  public onUpdate(): void {
    this._mousePosVe.value = new BABYLON.Vector2(this.scene.pointerX, this.scene.pointerY);
    this._mousePosX.value = this._mousePosVe.value.x;
    this._mousePosY.value = this._mousePosVe.value.y;
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 1, 2)) {
      return false;
    } else {
      if (para_array.length == 1) {
        let para0: VeryParas = new VeryParas();
        if (!ParaParserUtility.Vector2Para(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[0])) {
          return false;
        }
        this._mousePosVe = para0.vector2;
      }
      else {
        if (para_array[0].toLowerCase() == "null" || para_array[0].toLowerCase() == "none") {

        }
        else {
          let para0: VeryParas = new VeryParas();
          if (!ParaParserUtility.NumberParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[0])) {
            return false;
          }
          this._mousePosX = para0.number;
        }
        if (para_array[1].toLowerCase() == "null" || para_array[1].toLowerCase() == "none") {

        }
        else {
          let para0: VeryParas = new VeryParas();
          if (!ParaParserUtility.NumberParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[1])) {
            return false;
          }
          this._mousePosY = para0.number;
        }
      }

      return true;
    }
  }
  public destroy(): void {

  }
}

export class Action_Mouse_Pick extends VE_ActionBehaviour {

  public get ID(): string {
    return "Mouse_Pick|鼠标点选";
  }

  public get className(): string {
    return "Action_Mouse_Pick";
  }

  private _blueBool: VeryBool = new VeryBool();
  private _isBool = false;

  private _blueGameObject: VeryGameObject = new VeryGameObject();
  private _isGameObject = false;

  private _blueVector3: VeryVector3 = new VeryVector3();
  private _isVector3 = false;

  private _blueFloat: VeryNumber = new VeryNumber();
  private _isFloat = false;

  private _observer: Nullable<BABYLON.Observer<BABYLON.PointerInfo>> = null;

  public active(): void {
    this._observer = this.scene.onPointerObservable.add((pointerInfo) => {
      switch (pointerInfo.type) {
        case BABYLON.PointerEventTypes.POINTERDOWN:
          if (pointerInfo.pickInfo != null) {
            if (this._isBool) {
              this._blueBool.value = (pointerInfo.pickInfo.pickedMesh != null);
            }
            if (this._isGameObject) {
              if (pointerInfo.pickInfo.pickedMesh != null) {
                this._blueGameObject.setValue(pointerInfo.pickInfo.pickedMesh);
              }
              else {
                this._blueGameObject.value = null;
              }
            }
            if (this._isVector3) {
              if (pointerInfo.pickInfo.pickedPoint != null) {
                this._blueVector3.value = pointerInfo.pickInfo.pickedPoint;
              }
              else {
                this._blueVector3.value = BABYLON.Vector3.Zero();
              }
            }
            if (this._isFloat) {
              this._blueFloat.value = pointerInfo.pickInfo.distance;
            }
          }
          break;
      };
    });
  }

  public onUpdate(): void {

  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 4)) {
      return false;
    } else {
      //参数1
      if (para_array[0].toLowerCase() == "null" || para_array[0].toLowerCase() == "none") {

      }
      else {
        this._isBool = true;
        var para0 = new VeryParas();
        if (!ParaParserUtility.BoolParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[0])) {
          return false;
        }
        this._blueBool = para0.bool;
      }

      //参数2
      if (para_array[1].toLowerCase() == "null" || para_array[1].toLowerCase() == "none") {

      }
      else {
        this._isGameObject = true;
        var para0 = new VeryParas();
        if (!ParaParserUtility.GameObjectParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[1])) {
          return false;
        }
        this._blueGameObject = para0.gameObject;
      }

      //参数3
      if (para_array[2].toLowerCase() == "null" || para_array[2].toLowerCase() == "none") {

      }
      else {
        this._isVector3 = true;
        var para0 = new VeryParas();
        if (!ParaParserUtility.Vector3Para(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[2])) {
          return false;
        }
        this._blueVector3 = para0.vector3;
      }

      //参数4
      if (para_array[3].toLowerCase() == "null" || para_array[3].toLowerCase() == "none") {

      }
      else {
        this._isFloat = true;
        var para0 = new VeryParas();
        if (!ParaParserUtility.NumberParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[3])) {
          return false;
        }
        this._blueFloat = para0.number;
      }

      return true;
    }
  }
  public destroy(): void {
    if (this._observer) {
      this.scene.onPointerObservable.remove(this._observer);
    }
    this._observer = null;
  }
}

export class Action_MouseDrag extends VE_ActionBehaviour {

  public get ID(): string {
    return "MouseDrag|鼠标拖拽";
  }

  public get className(): string {
    return "Action_MouseDrag";
  }

  //拖拽方式
  private _choose = "";
  private _moveRef: BABYLON.Nullable<Transform> = null;
  //拖拽鼠标按钮
  private _mouseBtn = 0;
  //拖拽轴
  private _move = "";
  //过滤条件
  private _filter = false;
  private _include = true;
  private _filterName: string[] = [];

  //拖拽
  private startingPoint: BABYLON.Nullable<BABYLON.Vector2> = null;
  private currentMesh: BABYLON.Nullable<BABYLON.AbstractMesh> = null;

  private _observer: Nullable<BABYLON.Observer<BABYLON.PointerInfo>> = null;

  public active(): void {
    this._observer = this.scene.onPointerObservable.add((pointerInfo) => {
      switch (pointerInfo.type) {
        case BABYLON.PointerEventTypes.POINTERDOWN:
          if (pointerInfo.pickInfo && pointerInfo.pickInfo.pickedMesh) {
            if (!this._filter) {
              this.pointerDown(pointerInfo.pickInfo.pickedMesh)
            }
            else {
              if (this._include) {
                if (this._filterName.indexOf(pointerInfo.pickInfo.pickedMesh.name) != -1) {
                  this.pointerDown(pointerInfo.pickInfo.pickedMesh)
                }
              }
              else {
                if (this._filterName.indexOf(pointerInfo.pickInfo.pickedMesh.name) == -1) {
                  this.pointerDown(pointerInfo.pickInfo.pickedMesh)
                }
              }
            }
          }
          break;
        case BABYLON.PointerEventTypes.POINTERUP:
          this.pointerUp();
          break;
        case BABYLON.PointerEventTypes.POINTERMOVE:
          this.pointerMove();
          break;
      }
    });
  }
  private getPosition(): BABYLON.Nullable<BABYLON.Vector2> {
    return new BABYLON.Vector2(this.scene.pointerX, this.scene.pointerY);
  }
  private pointerDown(mesh: BABYLON.AbstractMesh) {
    this.currentMesh = mesh;
    let tmpCamera = this.scene.activeCamera;
    this.startingPoint = this.getPosition();
    if (this.startingPoint) { // we need to disconnect camera from canvas
      setTimeout(function () {
        if (tmpCamera)
          tmpCamera.detachControl(BabylonEngine.Canvas);
      }, 0);
    }
  }

  private pointerUp() {
    if (this.startingPoint) {
      let tmpCamera = this.scene.activeCamera;
      if (tmpCamera)
        tmpCamera.attachControl(BabylonEngine.Canvas, true);
      this.startingPoint = null;
      return;
    }
  }

  private pointerMove() {
    if (!this.startingPoint) {
      return;
    }
    var current = this.getPosition();
    if (!current) {
      return;
    }

    var diff = current.subtract(this.startingPoint);
    var tmpCamera = this.scene.activeCamera;
    //物体拖拽移动
    if (this.currentMesh && tmpCamera) {
      //free模式
      if (this._choose == "free") {
        this.currentMesh.setAbsolutePosition(this.currentMesh.getAbsolutePosition().add(tmpCamera.getDirection(new BABYLON.Vector3(1, 0, 0)).normalize().scale(diff.x)));
        this.currentMesh.setAbsolutePosition(this.currentMesh.getAbsolutePosition().add(tmpCamera.getDirection(new BABYLON.Vector3(0, -1, 0)).normalize().scale(diff.y)));
      }
      //TODO:World, Self和物体模式未实现;x,y,z,xy,xz,yz轴6种模式未实现
      if (this._choose == "world") {
        this.currentMesh.setAbsolutePosition(this.currentMesh.getAbsolutePosition().add(tmpCamera.getDirection(new BABYLON.Vector3(1, 0, 0)).normalize().scale(diff.x)));
        this.currentMesh.setAbsolutePosition(this.currentMesh.getAbsolutePosition().add(tmpCamera.getDirection(new BABYLON.Vector3(0, -1, 0)).normalize().scale(diff.y)));
      }
    }
    this.startingPoint = current;
  }

  public onUpdate(): void {

  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 3, 5)) {
      return false;
    } else {
      //参数1
      var firstStr = para_array[0].toLowerCase();
      if (firstStr == "world" || firstStr == "世界") {
        this._choose = "world";
      }
      else if (firstStr == "self" || firstStr == "相对" || firstStr == "自身") {
        this._choose = "self";
      }
      else if (firstStr == "free" || firstStr == "自由") {
        this._choose = "free";
      }
      else {
        var tempObj = new VeryGameObject();
        let para1 = new VeryParas();
        if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
          return false;
        }
        tempObj = para1.gameObject;
        if (tempObj.value == null) {
          console.error(this.ID + " 响应传入参数格式错误，当前物体： " + para_array[0] + "，无法在场景中查找到，请检查！");
          return false;
        }
        this._moveRef = tempObj.value.transform;
        this._choose = "someObject";
      }

      //参数2
      var secondStr = para_array[1].toLowerCase();
      if (secondStr == "0" || secondStr == "left" || secondStr == "左") {
        this._mouseBtn = 0;
      }
      else if (secondStr == "1" || secondStr == "right" || secondStr == "右") {
        this._mouseBtn = 2;
      }
      else if (secondStr == "2" || secondStr == "middle" || secondStr == "中") {
        this._mouseBtn = 1;
      }
      else {
        console.error(this.ID + " 响应传入参数格式错误，当前值为： " + para_array[1] + "，鼠标参数格式为:  2. “0”、“left”或者“左”表示鼠标左键拖拽，填“1”、“right”或者“右”表示鼠标右键拖拽，填“2”、“middle”或者“中”表示鼠标中键拖拽！");
        return false;
      }

      //参数3
      var thirdStr = para_array[2].toLowerCase();
      if (thirdStr == "x" || thirdStr == "y" || thirdStr == "z" || thirdStr == "xy" || thirdStr == "yx" || thirdStr == "xz" || thirdStr == "zx" || thirdStr == "yz" || thirdStr == "zy") {
        this._move = thirdStr;
      }
      else {
        if (this._choose != "free") {
          console.error(this.ID + " 响应传入参数格式错误： " + para_array[2] + "，轴应该填“X”、“Y”、“Z”、“XY”、“XZ”或者“YZ”！");
          return false;
        }
      }

      //参数4
      if (para_array.length > 3) {
        var tagArray = para_array[3].split('~');
        if (tagArray.length != 2) {
          console.error(this.ID + " 响应参数格式填写错误，第4个参数应为“物体名~包括”或者“Name~Include”、“物体名~排除”或者“Name~Exclude”，表示过滤条件！");
          return false;
        }
        //物体名或者标签判断
        if (tagArray[0].trim() == "物体名" || tagArray[0].trim().toLowerCase() == "name") {
        }
        else {
          console.error(this.ID + " 响应参数格式填写错误，第4个参数应为“物体名~包括”或者“Name~Include”、“物体名~排除”或者“Name~Exclude”，表示过滤条件！");
          return false;
        }
        //排除或者包括判断
        if (tagArray[1].trim() == "包括" || tagArray[1].trim().toLowerCase() == "include") {
          this._include = true;
        }
        else if (tagArray[1].trim() == "排除" || tagArray[1].trim().toLowerCase() == "exclude") {
          this._include = false;
        }
        else {
          console.error(this.ID + " 响应参数格式填写错误，第4个参数应为“物体名~包括”或者“Name~Include”、“物体名~排除”或者“Name~Exclude”，表示过滤条件！");
          return false;
        }
      }
      else {
        this._filter = false;
        this._filterName = [];
      }

      //参数5
      if (para_array.length > 4) {
        var nameArray = para_array[4].split(/,|，/);
        for (var i = 0; i < nameArray.length; i++) {
          nameArray[i] = nameArray[i].trim();
          if (VarTools.IsVarID(nameArray[i])) {
            var tempStr = new VeryString();
            let para1 = new VeryParas();
            if (!ParaParserUtility.StringParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, nameArray[i])) {
              return false;
            }
            tempStr = para1.string;
            if (tempStr.value != "" && (this._filterName.indexOf(tempStr.value) == -1)) {
              this._filterName[this._filterName.length] = (nameArray[i]);
            }
          }
          else {
            if (nameArray[i] != "" && (this._filterName.indexOf(nameArray[i]) == -1)) {
              this._filterName[this._filterName.length] = (nameArray[i]);
            }
          }
        }
        if (this._filterName.length > 0) {
          this._filter = true;
        }
      }
      else {
        this._filter = false;
        this._filterName = [];
      }
      return true;
    }
  }
  public destroy(): void {
    if (this._observer) {
      this.scene.onPointerObservable.remove(this._observer);
    }
    this._observer = null;
  }
}

export class Action_MouseEnterHighlight extends VE_ActionBehaviour {

  public get ID(): string {
    return "MouseEnterHighlight|鼠标移入高亮";
  }

  public get className(): string {
    return "Action_MouseEnterHighlight";
  }

  //鼠标经过时改变物体颜色  
  private _isColor = true;
  private _newColor: VeryVector3 = new VeryVector3();
  private _colorFloat: VeryNumber[] = [];
  //是否处理子物体
  private _isRecursion = true;
  //是否开启高亮
  private _switch: VeryBool = new VeryBool();
  //高亮物体
  private _objArray: string[] = [];

  //当前操作Mesh
  private _currentMesh: BABYLON.Nullable<BABYLON.AbstractMesh> = null;
  private _oldMaterial: BABYLON.Nullable<BABYLON.Material> = null;
  private _oldMaterials: { [key: string]: BABYLON.Nullable<BABYLON.Material> } = {};

  private _observer: Nullable<BABYLON.Observer<BABYLON.PointerInfo>> = null;

  public active(): void {
    this._observer = this.scene.onPointerObservable.add((pointerInfo) => {
      switch (pointerInfo.type) {
        case BABYLON.PointerEventTypes.POINTERMOVE:
          if (this._switch) {
            var pickinfo = this.scene.pick(this.scene.pointerX, this.scene.pointerY);
            if (pickinfo == null || pickinfo.pickedMesh == null) {
              if (this._currentMesh != null) {
                //console.log("Out--" + this._currentMesh + "--" + this._oldMaterial);
                if (this._isRecursion) {
                  this.RecursionRestore(this._currentMesh);
                  this._oldMaterials = {};
                }
                this._currentMesh.material = this._oldMaterial;
                this._currentMesh = null;
              }
            }
            else {
              if (this._currentMesh != pickinfo.pickedMesh) {
                if (this._currentMesh != null) {
                  if (this._isRecursion) {
                    this.RecursionRestore(this._currentMesh);
                    this._oldMaterials = {};
                  }
                  this._currentMesh.material = this._oldMaterial;
                  this._currentMesh = null;
                }

                if (this._objArray.indexOf(pickinfo.pickedMesh.name) != -1) {
                  this._oldMaterial = pickinfo.pickedMesh.material;
                  var tmpMat = new BABYLON.StandardMaterial("tmpMat", this.scene);
                  if (!this._isColor) {
                    tmpMat.diffuseColor = new BABYLON.Color3(this._colorFloat[0].value / 255, this._colorFloat[1].value / 255, this._colorFloat[2].value / 255);
                  }
                  else {
                    tmpMat.diffuseColor = new BABYLON.Color3(this._newColor.value.x / 255, this._newColor.value.y / 255, this._newColor.value.z / 255);
                  }
                  pickinfo.pickedMesh.material = tmpMat;
                  this._currentMesh = pickinfo.pickedMesh;
                  if (this._isRecursion) {
                    this.RecursionRecord(this._currentMesh,);
                  }
                  //console.log("Enter--" + this._currentMesh + "--" + this._oldMaterial);
                }
              }
            }
          }
          break;
      }
    });
  }

  private RecursionRecord(obj: BABYLON.AbstractMesh) {
    if (obj.getChildMeshes().length > 0) {
      for (var i = 0; i < obj.getChildMeshes().length; i++) {
        var tmpOldMat = obj.getChildMeshes()[i].material;
        var tmpMat = new BABYLON.StandardMaterial("tmpMat", this.scene);
        if (!this._isColor) {
          tmpMat.diffuseColor = new BABYLON.Color3(this._colorFloat[0].value / 255, this._colorFloat[1].value / 255, this._colorFloat[2].value / 255);
        }
        else {
          tmpMat.diffuseColor = new BABYLON.Color3(this._newColor.value.x / 255, this._newColor.value.y / 255, this._newColor.value.z / 255);
        }
        obj.getChildMeshes()[i].material = tmpMat;
        this._oldMaterials[obj.getChildMeshes()[i].name] = tmpOldMat;

        this.RecursionRecord(obj.getChildMeshes()[i]);
      }
    }
  }
  private RecursionRestore(obj: BABYLON.AbstractMesh) {
    if (obj.getChildMeshes().length > 0) {
      for (var i = 0; i < obj.getChildMeshes().length; i++) {
        obj.getChildMeshes()[i].material = this._oldMaterials[obj.getChildMeshes()[i].name];

        this.RecursionRestore(obj.getChildMeshes()[i]);
      }
    }
  }


  public onUpdate(): void {
    if (!this._switch) {
      if (this._currentMesh != null) {
        if (this._isRecursion) {
          this.RecursionRestore(this._currentMesh);
          this._oldMaterials = {};
        }
        this._currentMesh.material = this._oldMaterial;
        this._currentMesh = null;
      }
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 3, 4)) {
      return false;
    } else {
      //参数1
      var para0 = new VeryParas();
      if (!ParaParserUtility.BoolParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[0])) {
        return false;
      }
      this._switch = para0.bool;

      //参数3
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.Vector3OrNumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Trigger, para2, para_array[2])) {
        return false;
      }
      this._isColor = para2.isVec;
      this._newColor = para2.vector3;
      this._colorFloat = para2.vecNumbers;

      //参数4
      if (para_array.length > 3) {
        if (para_array[3].toLowerCase() == "false") {
          this._isRecursion = false;
        }
        else if (para_array[3].toLowerCase() != "true") {
          console.warn(this.ID + " 响应传入参数格式错误，其值为： " + para_array[3] + "，应为true或者false，当前值两者都不是，此处默认true处理，自动给子物体也高亮！");
        }
      }

      //参数2
      var nameArray = para_array[1].split(/,|，/);
      for (var i = 0; i < nameArray.length; i++) {
        nameArray[i] = nameArray[i].trim();
        let para1: VeryParas = new VeryParas();
        var tempObj = new VeryGameObject();
        if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, nameArray[i])) {
          return false;
        }
        tempObj = para1.gameObject
        if (tempObj.value == null) {
          console.error(this.ID + " 响应传入参数格式错误，当前物体： " + nameArray[i] + "，该物体无法在场景中查找到，请检查！");
          return false;
        }
        this._objArray[this._objArray.length] = nameArray[i];
      }

      return true;
    }
  }
  public destroy(): void {
    if (this._observer) {
      this.scene.onPointerObservable.remove(this._observer);
    }
    this._observer = null;
  }
}

export class Action_CursorControl extends VE_ActionBehaviour {

  public get ID(): string {
    return "CursorControl|鼠标控制";
  }

  public get className(): string {
    return "Action_CursorControl";
  }

  private _cursorControl: VeryBool = new VeryBool();

  public active(): void {
    if (this._cursorControl.value) {
      document.body.style.cursor = "default";
    }
    else {
      document.body.style.cursor = "none";
    }
  }

  public onUpdate(): void {

  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 1)) {
      return false;
    } else {
      let para0: VeryParas = new VeryParas();
      if (!ParaParserUtility.BoolParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[0])) {
        return false;
      }
      this._cursorControl = para0.bool;

      return true;
    }
  }
  public destroy(): void {

  }
}

