import { VE_ActionBehaviour, VE_Actions } from "../../action";
import { ParaParserUtility, VeryParas, ErrorInfo } from "../../utility";
import { ParaType } from "../../enum";
import { VeryNumber, VeryString, IVeryVar, VeryBool, VarTools } from "../../variables";

export class Action_String_To_Bool extends VE_ActionBehaviour {

  public get ID(): string {
    return "String_To_Bool|字符串转布尔值";
  }

  public get className(): string {
    return "Action_String_To_Bool";
  }

  private _blueString: VeryString = new VeryString();
  private _blueBool: VeryBool = new VeryBool();

  public active(): void {

  }

  public onUpdate(): void {
    try {
      this._blueBool.value = Boolean(this._blueString.value);
    }
    catch
    {
      console.error(this.ID + " 响应传入参数格式错误： " + this._blueString.value + "，请检查填入数据！");
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2)) {
      return false;
    } else {
      // 参数1：字符串
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._blueString = para1.string;

      // 参数2：接收bool变量
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.BoolParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._blueBool = para2.bool;

      return true;
    }
  }
  public destroy(): void {

  }
}

