export class Action_To_String extends VE_ActionBehaviour {

  public get ID(): string {
    return "To_String|转字符串";
  }

  public get className(): string {
    return "Action_To_String";
  }

  private _isBlueVar = false;
  private _str = "";
  private _iBlueVar: Nullable<IVeryVar> = null;
  private _blueString: VeryString = new VeryString();

  public active(): void {

  }

  public onUpdate(): void {
    if (this._isBlueVar && this._iBlueVar != null) {
      this._blueString.value = this._iBlueVar.getValue();
    }
    else {
      this._blueString.value = this._str;
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2)) {
      return false;
    } else {
      // 参数1：
      if (VarTools.IsVarID(para_array[0])) {
        this._isBlueVar = true;
        let errorInfo: ErrorInfo = new ErrorInfo();
        let veryVar: Nullable<IVeryVar> = VarTools.GetVeryVarWithExpression(this.projectName, this.objectID, para_array[0], errorInfo);
        if (veryVar == null) {
          console.error(this.ID + " 响应 传入参数错误，变量：" + para_array[0] + "，" + errorInfo);
        }
        else {
          this._iBlueVar = veryVar;
        }
      }
      else {
        this._str = para_array[0];
        this._isBlueVar = false;
      }

      // 参数2：字符串
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[1])) {
        return false;
      }
      this._blueString = para1.string;

      return true;
    }
  }

  public destroy(): void {

  }
}

//Convert
//TODO:Test
VE_Actions.addAction(Action_String_To_Bool);
VE_Actions.addAction(Action_String_To_Number);
VE_Actions.addAction(Action_To_String);