export class Action_Vector3_Set_Value extends VE_ActionBehaviour {

  public get ID(): string {
    return "Vector3_Set_Value|设置向量值 ";
  }

  public get className(): string {
    return "Action_Vector3_Set_Value";
  }

  private _acceptVec: VeryVector3 = new VeryVector3();

  private _isVec = false;
  private _blueVector3: VeryVector3 = new VeryVector3();
  private _vecFloat: VeryNumber[] = [];
  private _isSet: boolean[] = [false, false, false];


  public active(): void {

  }

  public onUpdate(): void {
    if (!this._isVec) {
      if (this._isSet[0]) {
        this._acceptVec.value = new BABYLON.Vector3(this._vecFloat[0].value, this._acceptVec.value.y, this._acceptVec.value.z);
      }
      if (this._isSet[1]) {
        this._acceptVec.value = new BABYLON.Vector3(this._acceptVec.value.x, this._vecFloat[1].value, this._acceptVec.value.z);
      }
      if (this._isSet[2]) {
        this._acceptVec.value = new BABYLON.Vector3(this._acceptVec.value.x, this._acceptVec.value.y, this._vecFloat[2].value);
      }
    }
    else {
      this._acceptVec.value = this._blueVector3.value;
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2)) {
      return false;
    } else {
      // 参数1:设置值
      if (para_array[0].indexOf(",") != -1 || para_array[0].indexOf("，") != -1) {
        this._isVec = false;
        var strArray: string[] = para_array[0].split(/,|，/);
        if (strArray.length == 3) {
          strArray[0] = strArray[0].trim();
          strArray[1] = strArray[1].trim();
          strArray[2] = strArray[2].trim();
          if (strArray[0].toLowerCase() == "none" || strArray[0].toLowerCase() == "null") {
            this._isSet[0] = false;
          }
          else {
            this._isSet[0] = true;
            let para0: VeryParas = new VeryParas();
            if (!ParaParserUtility.NumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para0, strArray[0])) {
              return false;
            }
            this._vecFloat[0] = para0.number;
          }

          if (strArray[1].toLowerCase() == "none" || strArray[1].toLowerCase() == "null") {
            this._isSet[1] = false;
          }
          else {
            this._isSet[1] = true;
            let para0: VeryParas = new VeryParas();
            if (!ParaParserUtility.NumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para0, strArray[1])) {
              return false;
            }
            this._vecFloat[1] = para0.number;
          }

          if (strArray[2].toLowerCase() == "none" || strArray[2].toLowerCase() == "null") {
            this._isSet[2] = false;
          }
          else {
            this._isSet[2] = true;
            let para0: VeryParas = new VeryParas();
            if (!ParaParserUtility.NumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para0, strArray[2])) {
              return false;
            }
            this._vecFloat[2] = para0.number;
          }
        }
        else {
          console.error(this.ID + " 响应，vector3应由3个float组成，当前：" + para_array[0] + "，该变量不符合要求，请检查！");
          return false;
        }
      }
      else {
        let para1: VeryParas = new VeryParas();
        if (!ParaParserUtility.Vector3OrNumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
          return false;
        }
        this._isVec = para1.isVec;
        this._vecFloat = para1.vecNumbers;
        this._blueVector3 = para1.vector3;
      }

      // 参数2:接收vector变量
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.Vector3Para(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._acceptVec = para2.vector3;
      return true;
    }
  }

  public destroy(): void {

  }
}

export class Action_Vector3_Distance extends VE_ActionBehaviour {

  public get ID(): string {
    return "Vector3_Distance|计算距离";
  }

  public get className(): string {
    return "Action_Vector3_Distance";
  }

  private _isVec1: boolean = true;
  private _vec1: VeryVector3 = new VeryVector3();
  private _x1: VeryNumber = new VeryNumber();
  private _y1: VeryNumber = new VeryNumber();
  private _z1: VeryNumber = new VeryNumber();


  private _isVec2: boolean = true;
  private _vec2: VeryVector3 = new VeryVector3();
  private _x2: VeryNumber = new VeryNumber();
  private _y2: VeryNumber = new VeryNumber();
  private _z2: VeryNumber = new VeryNumber();

  private _result: VeryNumber = new VeryNumber();

  public active(): void {

  }

  public onUpdate(): void {
    if (this._isVec1 && this._isVec2) {
      this._result.value = BABYLON.Vector3.Distance(this._vec1.value, this._vec2.value);
    }
    else if (this._isVec1 && !this._isVec2) {
      this._result.value = BABYLON.Vector3.Distance(this._vec1.value, new BABYLON.Vector3(this._x2.value, this._y2.value, this._z2.value));
    }
    else if (!this._isVec1 && this._isVec2) {
      this._result.value = BABYLON.Vector3.Distance(new BABYLON.Vector3(this._x1.value, this._y1.value, this._z1.value), this._vec2.value);
    }
    else {
      this._result.value = BABYLON.Vector3.Distance(new BABYLON.Vector3(this._x1.value, this._y1.value, this._z1.value), new BABYLON.Vector3(this._x2.value, this._y2.value, this._z2.value));
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 3)) {
      return false;
    } else {
      // 参数1:向量1
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.Vector3OrNumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._isVec1 = para1.isVec;
      this._x1.value = para1.vecNumbers[0].value;
      this._y1.value = para1.vecNumbers[1].value;
      this._z1.value = para1.vecNumbers[2].value;
      this._vec1 = para1.vector3;

      // 参数2:向量2
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.Vector3OrNumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._isVec2 = para2.isVec;
      this._x2.value = para2.vecNumbers[0].value;
      this._y2.value = para2.vecNumbers[1].value;
      this._z2.value = para2.vecNumbers[2].value;
      this._vec2 = para2.vector3;

      // 参数3:结果值
      let para3: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para3, para_array[2])) {
        return false;
      }
      this._result = para3.number;
      return true;
    }
  }

  public destroy(): void {

  }
}

