export class Action_Vector3_Operator extends VE_ActionBehaviour {

  public get ID(): string {
    return "Vector3_Operator|向量运算";
  }

  public get className(): string {
    return "Action_Vector3_Operator";
  }
  //private enum OperatorType { Add, Angle, CrossProduct, Divide, Distance, DotProduct, Max, Min, Multiply, Project, Reflect, Substract, None }
  //private OperatorType _currentType = OperatorType.None;
  //private _typeDic = {};

  private _currentType: string = "";

  private _BlueVecSet: boolean = false;
  private _BlueFloatSet: boolean = false;

  private _isFloatOp: boolean = false;

  private _resultVec: VeryVector3 = new VeryVector3();
  private _resultFloat: VeryNumber = new VeryNumber();

  private _isVec1: boolean = false;
  private _vecUnit1: VeryVector3 = new VeryVector3();
  private _vecFloat1: VeryNumber[] = [];

  private _isVec2: boolean = false;
  private _vecUnit2: VeryVector3 = new VeryVector3();
  private _vecFloat2: VeryNumber[] = [];
  private _float2: VeryNumber = new VeryNumber();


  public active(): void {

  }

  public onUpdate(): void {
    var vec1 = this._vecUnit1.value;
    if (!this._isVec1) {
      vec1 = new BABYLON.Vector3(this._vecFloat1[0].value, this._vecFloat1[1].value, this._vecFloat1[2].value);
    }

    var vec2 = this._vecUnit2.value;
    if (!this._isVec2) {
      vec2 = new BABYLON.Vector3(this._vecFloat2[0].value, this._vecFloat2[1].value, this._vecFloat2[2].value);
    }

    if (this._currentType == "加法") {
      if (this._BlueVecSet) {
        this._resultVec.value = vec1.add(vec2);
      }
    }
    else if (this._currentType == "计算角度") {
      if (this._BlueFloatSet) {
        //TODO:
        //this._resultFloat.value = BABYLON.Vector3.GetAngleBetweenVectors(vec1, vec2);
      }
    }
    else if (this._currentType == "叉积") {
      if (this._BlueVecSet) {
        this._resultVec.value = BABYLON.Vector3.Cross(vec1, vec2);
      }
    }
    else if (this._currentType == "距离") {
      if (this._BlueFloatSet) {
        this._resultFloat.value = BABYLON.Vector3.Distance(vec1, vec2);
      }
    }
    else if (this._currentType == "除法") {
      if (this._BlueVecSet && this._float2.value != 0) {
        this._resultVec.value = vec1.scale(1 / this._float2.value);
      }
    }
    else if (this._currentType == "点积") {
      if (this._BlueFloatSet) {
        this._resultFloat.value = BABYLON.Vector3.Dot(vec1, vec2);
      }
    }
    else if (this._currentType == "最大值") {
      if (this._BlueVecSet) {
        this._resultVec.value = BABYLON.Vector3.Maximize(vec1, vec2);
      }
    }
    else if (this._currentType == "最小值") {
      if (this._BlueVecSet) {
        this._resultVec.value = BABYLON.Vector3.Minimize(vec1, vec2);
      }
    }
    else if (this._currentType == "乘法") {
      if (this._BlueVecSet) {
        this._resultVec.value = vec1.scale(this._float2.value);
      }
    }
    else if (this._currentType == "投影") {
      if (this._BlueVecSet) {
        //TODO:
        //this._resultVec.value = BABYLON.Vector3.Project(vec1, vec2);
      }
    }
    else if (this._currentType == "反射") {
      if (this._BlueVecSet) {
        //TODO:
        //this._resultVec.value = BABYLON.Vector3.Reflect(vec1, vec2);
      }
    }
    else if (this._currentType == "减法") {
      if (this._BlueVecSet) {
        this._resultVec.value = vec1.subtract(vec2);
      }
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 5)) {
      return false;
    } else {
      // 参数1:向量1
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.Vector3OrNumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._isVec1 = para1.isVec;
      this._vecFloat1 = para1.vecNumbers;
      this._vecUnit1 = para1.vector3;

      // 参数2:计算
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.Vector3OrNumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        if (!ParaParserUtility.NumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
          return false;
        }
        this._isFloatOp = true;
        this._float2 = para2.number;
      }
      else {
        this._isFloatOp = true;
        this._isVec2 = para2.isVec;
        this._vecFloat2 = para2.vecNumbers;
        this._vecUnit2 = para2.vector3;
      }

      //参数3:
      if (this._currentType != "加法" && this._currentType != "计算角度" &&
        this._currentType != "叉积" && this._currentType != "除法" &&
        this._currentType != "距离" && this._currentType != "点积" &&
        this._currentType != "最大值" && this._currentType != "最小值" &&
        this._currentType != "乘法" && this._currentType != "投影" &&
        this._currentType != "反射" && this._currentType != "减法") {
        console.error(this.ID + " 响应传入参数格式错误，值为： " + para_array[2] + "，系统中没有该运算方式，请查看填写是否正确！");
        return false;
      }
      //第二个参数类型检查
      if (this._currentType == "除法" || this._currentType == "乘法")  //第二个参数为float
      {
        if (!this._isFloatOp) {
          console.error(this.ID + " 响应传入参数格式错误，向量运算方式为： " + para_array[2] + "，对应的第2个参数应为float类型！");
          return false;
        }
      }
      else {
        if (this._isFloatOp) {
          console.error(this.ID + " 响应传入参数格式错误，向量运算方式为： " + para_array[2] + "，对应的第2个参数应为Vector3类型！");
          return false;
        }
      }

      // 参数4:接收向量
      if (para_array[3].toLowerCase() == "none" || para_array[3].toLowerCase() == "null") {
        this._BlueVecSet = false;
      }
      else {
        this._BlueVecSet = true;
        let para3: VeryParas = new VeryParas();
        if (!ParaParserUtility.Vector3Para(this.projectName, this.objectID, this.ID, ParaType.Action, para3, para_array[3])) {
          return false;
        }
        this._resultVec = para3.vector3;
      }

      // 参数5:接收number
      if (para_array[4].toLowerCase() == "none" || para_array[4].toLowerCase() == "null") {
        this._BlueFloatSet = false;
      }
      else {
        this._BlueFloatSet = true;
        let para4: VeryParas = new VeryParas();
        if (!ParaParserUtility.NumberPara(this.projectName, this.objectID, this.ID, ParaType.Action, para4, para_array[4])) {
          return false;
        }
        this._resultFloat = para4.number;
      }
      return true;
    }
  }

  public destroy(): void {

  }
}

