import { VE_ActionBehaviour, VE_Actions } from "../../action";
import { ParaParserUtility, VE_KeyboardMap, VeryParas } from '../../utility';
import { ParaType, Severity } from '../../enum';
import { VE_ErrorManager, VE_Error } from '../../global';
import { VeryGameObject, VeryBool, VeryNumber, VeryString, VeryRect, VeryVector3, VeryVector2, VeryMaterial } from "../../variables";

export class Action_AudioPlay extends VE_ActionBehaviour {

  public get ID(): string {
    return 'AudioPlay|声音播放';
  }

  public get className(): string {
    return 'Action_AudioPlay';
  }


  //是否开启
  private _switch: VeryBool = new VeryBool();
  //播放音乐
  private _musicString: VeryString = new VeryString();
  //音乐循环
  private _musicLoop: VeryBool = new VeryBool();
  //音乐大小
  private _musicVolume: VeryNumber = new VeryNumber();

  //声音
  private _music: BABYLON.Nullable<BABYLON.Sound> = null;

  public active(): void {
    if (this._music != null) {
      if (this._switch.value) {
        this._music.loop = this._musicLoop.value;
        this._music.setVolume(this._musicVolume.value);
        if (!this._music.isPlaying) {
          this._music.play();
        }
      }
      else {
        this._music.loop = this._musicLoop.value;
        this._music.setVolume(this._musicVolume.value);
        this._music.stop();
      }
    }

  }

  public onUpdate(): void {

  }


  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Trigger, para_array.length, 4)) {
      return false;
    } else {
      // 第1个参数：
      let para0: VeryParas = new VeryParas();
      if (!ParaParserUtility.BoolParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[0])) {
        return false;
      }
      this._switch = para0.bool;

      //参数2
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[1])) {
        return false;
      }
      this._musicString = para1.string;
      //console.log("_musicString--" + this._musicString.value);
      this._music = new BABYLON.Sound("Music", this._musicString.value, this.scene, null, {
        loop: false,
        autoplay: false
      });

      //参数3
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.BoolParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[2])) {
        return false;
      }
      this._musicLoop = para2.bool;

      //参数4
      let para3: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para3, para_array[3])) {
        return false;
      }
      this._musicVolume = para3.number;

      return true;
    }

  }
  public destroy(): void {

  }
}

export class Action_WindowsVoice extends VE_ActionBehaviour {

  public get ID(): string {
    return 'WindowsVoice|语音';
  }

  public get className(): string {
    return 'Action_WindowsVoice';
  }

  //播放文本
  private _musicString: VeryString = new VeryString();

  public active(): void {
    var utterThis = new window.SpeechSynthesisUtterance(this._musicString.value);
    window.speechSynthesis.speak(utterThis);
  }

  public onUpdate(): void {

  }


  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Trigger, para_array.length, 1)) {
      return false;
    } else {
      //参数1
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._musicString = para1.string;

      return true;
    }

  }
  public destroy(): void {

  }
}

//TODO:20200811
VE_Actions.addAction(Action_AudioPlay);
VE_Actions.addAction(Action_WindowsVoice);