export class Action_Bool_Operator extends VE_ActionBehaviour {

  public get ID(): string {
    return "Bool_Operator|布尔值计算";
  }

  public get className(): string {
    return "Action_Bool_Operator";
  }

  public get type(): ActionType {
    return ActionType.Animation;
  }

  private _blueBool1: VeryBool = new VeryBool();
  private _blueBool2: VeryBool = new VeryBool();
  private _resultBool: VeryBool = new VeryBool();
  private _mode = "";

  public active(): void {
    if (this._mode == "and") {
      this._resultBool.value = this._blueBool1.value && this._blueBool2.value;
    }
    else if (this._mode == "or") {
      this._resultBool.value = this._blueBool1.value || this._blueBool2.value;
    }
    else if (this._mode == "nand") {
      this._resultBool.value = !(this._blueBool1.value && this._blueBool2.value);
    }
    else if (this._mode == "xor") {
      this._resultBool.value = !(this._blueBool1.value == this._blueBool2.value);
    }
    else if (this._mode == "nor") {
      this._resultBool.value = !(this._blueBool1.value || this._blueBool2.value);
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 4)) {
      return false;
    } else {
      // 参数1：
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.BoolParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._blueBool1 = para1.bool;

      // 参数2：
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.BoolParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._blueBool2 = para2.bool;

      // 参数3：
      if (para_array[2].toLowerCase() == "and" || para_array[2] == "与") {
        this._mode = "and";
      }
      else if (para_array[2].toLowerCase() == "or" || para_array[2] == "或") {
        this._mode = "or";
      }
      else if (para_array[2].toLowerCase() == "nand" || para_array[2] == "与非") {
        this._mode = "nand";
      }
      else if (para_array[2].toLowerCase() == "xor" || para_array[2] == "异或") {
        this._mode = "xor";
      }
      else if (para_array[2].toLowerCase() == "nor" || para_array[2] == "与或") {
        this._mode = "nor";
      }
      else {
        console.error(this.ID + " 响应传入参数格式错误： " + para_array[2] + "，请参考填写说明！");
        return false;
      }

      // 参数4：
      let para4: VeryParas = new VeryParas();
      if (!ParaParserUtility.BoolParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para4, para_array[3])) {
        return false;
      }
      this._resultBool = para4.bool;

      return true;
    }
  }
  public destroy(): void {

  }
}
