export class Action_Number_Mutiple_Add extends VE_ActionBehaviour {

  public get ID(): string {
    return "Number_Mutiple_Add|多个数值加法";
  }

  public get className(): string {
    return "Action_Number_Mutiple_Add";
  }

  public get type(): ActionType {
    return ActionType.Animation;
  }

  private _blueFloatList: VeryNumber[] = [];
  private _acceptNumber: VeryNumber = new VeryNumber();

  public active(): void {
    this._acceptNumber.value = 0;
    for (var i = 0; i < this._blueFloatList.length; i++) {
      this._acceptNumber.value += this._blueFloatList[i].value;
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2)) {
      return false;
    } else {
      // 参数1：
      var strArray: string[] = para_array[0].split(/,|，/);
      let para1: VeryParas = new VeryParas();
      for (var i = 0; i < strArray.length; i++) {
        strArray[i] = strArray[i].trim();
        var tempFloat = new VeryNumber();
        if (!ParaParserUtility.NumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, strArray[i])) {
          return false;
        }
        tempFloat = para1.number;
        this._blueFloatList[this._blueFloatList.length] = (tempFloat);
      }

      // 参数2：
      let para3: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para3, para_array[1])) {
        return false;
      }
      this._acceptNumber = para3.number;

      return true;
    }
  }
  public destroy(): void {

  }
}
