import { VE_ActionBehaviour, VE_Actions } from "../../action";
import { ParaParserUtility, VeryParas, ErrorInfo } from "../../utility";
import { ParaType } from "../../enum";
import { VeryNumber, VeryString, IVeryVar, VeryBool, VarTools } from "../../variables";

export class Action_Screen_Width extends VE_ActionBehaviour {

  public get ID(): string {
    return "Screen_Width|获取屏幕宽度";
  }

  public get className(): string {
    return "Action_Screen_Width";
  }

  private _blueNumber: VeryNumber = new VeryNumber();

  public active(): void {

  }

  public onUpdate(): void {
    var renderCanvas = document.getElementById("renderCanvas");
    if (renderCanvas != null && renderCanvas != undefined)
      this._blueNumber.value = renderCanvas.clientWidth;
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 1)) {
      return false;
    } else {
      // 参数1：字符串
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._blueNumber = para1.number;

      return true;
    }
  }
  public destroy(): void {

  }
}

