export class Action_Open_URL extends VE_ActionBehaviour {

  public get ID(): string {
    return "Open_URL|打开网页";
  }

  public get className(): string {
    return "Action_Open_URL";
  }

  private _URL: VeryString = new VeryString();

  public active(): void {
    window.open(this._URL.value);
  }


  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 1)) {
      return false;
    } else {
      // 参数1：字符串
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._URL = para1.string;

      return true;
    }
  }
  public destroy(): void {

  }
}

export class Action_Quit extends VE_ActionBehaviour {

  public get ID(): string {
    return "Quit|退出程序";
  }

  public get className(): string {
    return "Action_Quit";
  }

  private _URL: VeryString = new VeryString();

  public active(): void {
    window.close();
  }


  public paraParser(para_array: string[]): boolean {
    return true;
  }
  public destroy(): void {

  }
}

//Convert
VE_Actions.addAction(Action_Open_URL);
//TODO:Firefox里面需要通过about:config将allow_scripts_to_close_windows设置成true才有效
VE_Actions.addAction(Action_Quit);
//TODO:渲染BabylonJS的Canvas名字不一定是renderCanvas
VE_Actions.addAction(Action_Screen_Width);
VE_Actions.addAction(Action_Screen_Height);