import { VE_ActionBehaviour, VE_Actions } from "../../action";
import { ParaParserUtility, VeryParas } from "../../utility";
import { ParaType } from "../../enum";
import { VeryNumber, VeryString, VeryVector3 } from "../../variables";

export class Action_String_Get_Length extends VE_ActionBehaviour {

  public get ID(): string {
    return "String_Get_Length|获取字符串长度";
  }

  public get className(): string {
    return "Action_String_Get_Length";
  }

  private _blueString: VeryString = new VeryString();
  private _blueInt: VeryNumber = new VeryNumber();

  public active(): void {

  }

  public onUpdate(): void {
    this._blueInt.setValue(this._blueString.value.length);
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2)) {
      return false;
    } else {
      // 参数1：字符串
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._blueString = para1.string;

      // 参数2：接收字符串的长度变量
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._blueInt = para2.number;

      return true;
    }
  }

  public destroy(): void {

  }

}

