export class Action_String_Add extends VE_ActionBehaviour {

  public get ID(): string {
    return "String_Add|字符串加法";
  }

  public get className(): string {
    return "Action_String_Add";
  }

  private _blueString: VeryString = new VeryString();
  private _stringArray: VeryString[] = [];

  public active(): void {
    var s: string = "";
    for (var i = 0; i < this._stringArray.length; i++) {
      s += this._stringArray[i].value;
    }
    this._blueString.setValue(s);
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2)) {
      return false;
    } else {
      // 参数1：待相加的字符串
      var strArray: string[] = para_array[0].split(/,|，/);
      for (var i = 0; i < strArray.length; i++) {
        strArray[i] = strArray[i].trim();
        var tempStr: VeryString = new VeryString();
        let para1: VeryParas = new VeryParas();
        if (!ParaParserUtility.StringParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, strArray[i])) {
          return false;
        }
        tempStr = para1.string;
        this._stringArray[this._stringArray.length] = tempStr;
      }

      // 参数2：接收字符串的字符串变量
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._blueString = para2.string;

      return true;
    }
  }

  public destroy(): void {

  }

}

export class Action_String_Replace extends VE_ActionBehaviour {

  public get ID(): string {
    return "String_Replace|字符串替换";
  }

  public get className(): string {
    return "Action_String_Replace";
  }

  private _originString: VeryString = new VeryString();

  private _blueString1: VeryString = new VeryString();

  private _blueString2: VeryString = new VeryString();

  private _blueString: VeryString = new VeryString();

  public active(): void {
    var reg = new RegExp(this._blueString1.value, "g");
    this._blueString.setValue(this._originString.value.replace(reg, this._blueString2.value));
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 4)) {
      return false;
    } else {
      // 参数1：原始字符串
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._originString = para1.string;
      // 参数2：被替换的字符串
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringPara(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._blueString1 = para2.string;
      // 参数3：替换字符串
      let para3: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringPara(this.projectName, this.objectID, this.ID, ParaType.Action, para3, para_array[2])) {
        return false;
      }
      this._blueString2 = para3.string;

      // 参数4：接收字符串的字符串变量
      let para4: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para4, para_array[3])) {
        return false;
      }
      this._blueString = para4.string;

      return true;
    }
  }

  public destroy(): void {

  }

}

export class Action_String_SubString extends VE_ActionBehaviour {

  public get ID(): string {
    return "String_SubString|截取字符串";
  }

  public get className(): string {
    return "Action_String_SubString";
  }

  private _originString: VeryString = new VeryString();

  private _index: VeryNumber = new VeryNumber();

  private _length: VeryNumber = new VeryNumber();

  private _blueString: VeryString = new VeryString();

  public active(): void {
    if (this._length.value == -1) {
      this._blueString.setValue(this._originString.value);
    }
    else {
      try {
        this._blueString.setValue(this._originString.value.substring(this._index.value, this._index.value + this._length.value));
      }
      catch
      {
        console.error(this.ID + " 响应传入参数格式错误： " + "参数2和参数3，可能是起始位置错误，可能是截取长度超出字符串长度，请检查！");
      }
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 4)) {
      return false;
    } else {
      // 参数1：原始字符串
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._originString = para1.string;
      // 参数2：截取开始的索引
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberPara(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._index = para2.number;
      // 参数3：截取长度
      if (para_array[2].toLowerCase() == "null" || para_array[2].toLowerCase() == "none" || para_array[2] == "-1") {
        this._length.setValue(-1);
      }
      else {
        let para3: VeryParas = new VeryParas();
        if (!ParaParserUtility.NumberPara(this.projectName, this.objectID, this.ID, ParaType.Action, para3, para_array[2])) {
          return false;
        }
        this._length = para3.number;
      }

      // 参数4：接收字符串的字符串变量
      let para4: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para4, para_array[3])) {
        return false;
      }
      this._blueString = para4.string;

      return true;
    }
  }

  public destroy(): void {

  }

}

//String
VE_Actions.addAction(Action_String_Get_Length);
VE_Actions.addAction(Action_String_Set_Value);
VE_Actions.addAction(Action_String_Add);
VE_Actions.addAction(Action_String_Replace);
VE_Actions.addAction(Action_String_SubString);