import { VE_ActionBehaviour, VE_Actions } from "../../action";
import { ParaParserUtility, VeryParas } from "../../utility";
import { ParaType } from "../../enum";
import { VeryNumber, VeryString, VeryBool, VeryGameObject } from "../../variables";
import { GameObject } from "../../babylon";

export class Action_Set_Active extends VE_ActionBehaviour {

  public get ID(): string {
    return "Set_Active|设置激活";
  }

  public get className(): string {
    return "Action_Set_Active";
  }

  private _obj: VeryGameObject = new VeryGameObject();
  private _isActive: VeryBool = new VeryBool();
  private _childActive: VeryBool = new VeryBool();

  public active(): void {
    if (this._obj.value != null) {
      if (this._childActive.value) {
        this.ActiveRecursion(this._obj.value.transform.transformNode, this._isActive.value);
      }
      else {
        this._obj.value.transform.transformNode?.setEnabled(this._isActive.value);
      }
    }
  }
  private ActiveRecursion(obj: BABYLON.Nullable<BABYLON.TransformNode>, active: boolean) {
    obj?.setEnabled(active);
    if (obj != null && obj.getChildTransformNodes().length > 0) {
      for (var i = 0; i < obj.getChildTransformNodes().length; i++) {
        this.ActiveRecursion(obj.getChildTransformNodes()[i], active);
      }
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2, 3)) {
      return false;
    } else {
      // 参数1：是否激活
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.BoolParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._isActive = para1.bool;

      // 参数2：物体
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._obj = para2.gameObject;

      //参数3：是否设置子物体
      if (para_array.length > 2) {
        let para3: VeryParas = new VeryParas();
        if (!ParaParserUtility.BoolParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para3, para_array[2])) {
          return false;
        }
        this._childActive = para3.bool;
      }

      return true;
    }
  }

  public destroy(): void {

  }
}
export class Action_Find extends VE_ActionBehaviour {

  public get ID(): string {
    return "Find|查找物体";
  }

  public get className(): string {
    return "Action_Find";
  }

  private _objName: VeryString = new VeryString();
  private _blueObj: VeryGameObject = new VeryGameObject();

  public active(): void {
    this._blueObj.value = GameObject.Find(this._objName.value);
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2)) {
      return false;
    } else {
      // 参数1：物体名
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._objName = para1.string;

      // 参数2:接收物体变量
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._blueObj = para2.gameObject;

      return true;
    }
  }

  public destroy(): void {

  }
}

export class Action_Destroy_Object extends VE_ActionBehaviour {

  public get ID(): string {
    return "Destroy_Object|删除物体";
  }

  public get className(): string {
    return "Action_Destroy_Object";
  }

  private _blueDeleObj: VeryGameObject = new VeryGameObject();
  private _time: VeryNumber = new VeryNumber();
  private _childFlag: VeryBool = new VeryBool();

  public active(): void {
    if (this._time.value <= 0) {
      if (this._childFlag.value) {
        this.RemoveParent();
        this.DestroyObj();
      }
      else {
        this.DestroyObj();
      }
    }
    else {
      if (this._childFlag.value) {
        this.RemoveParent();
        setTimeout(() => {
          this.DestroyObj();
        }, this._time.value * 1000);
      }
      else {
        setTimeout(() => {
          this.DestroyObj();
        }, this._time.value * 1000);
      }
    }
  }

  public RemoveParent() {
    var obj = this._blueDeleObj.value?.transform.transformNode;
    if (obj != null && obj.getChildTransformNodes().length > 0) {
      for (var i = 0; i < obj.getChildTransformNodes().length; i++) {
        obj.getChildTransformNodes()[i].setParent(null);
      }
    }
  }

  public DestroyObj() {
    if (this._blueDeleObj.value != null)
      GameObject.Destroy(this._blueDeleObj.value);
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 3)) {
      return false;
    } else {
      // 参数1：物体
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._blueDeleObj = para1.gameObject;

      // 参数2：延迟时间
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._time = para2.number;
      if (this._time.value < 0) {
        this._time.value = 0;
      }

      // 参数3：是否保留子物体
      let para3: VeryParas = new VeryParas();
      if (!ParaParserUtility.BoolParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para3, para_array[2])) {
        return false;
      }
      this._childFlag = para3.bool;

      return true;
    }
  }

  public destroy(): void {

  }
}

export class Action_Find_Child extends VE_ActionBehaviour {

  public get ID(): string {
    return "Find_Child|查找子物体";
  }

  public get className(): string {
    return "Action_Find_Child";
  }

  private _parentObj: VeryGameObject = new VeryGameObject();
  private _childStr: VeryString = new VeryString();
  private _blueObj: VeryGameObject = new VeryGameObject();


  public active(): void {
    this._blueObj.value = null;
    if (this._parentObj.value != null) {
      this.ChildRecursion(this._parentObj.value?.transform.transformNode, this._childStr.value);
    }
  }
  private ChildRecursion(parent: BABYLON.Nullable<BABYLON.TransformNode>, child_name: string) {
    if (parent == null) {
      return;
    }
    if (parent.name == child_name) {
      this._blueObj.value = GameObject.Find(child_name);
      return;
    }
    if (parent != null && parent.getChildTransformNodes().length > 0) {
      for (var i = 0; i < parent.getChildTransformNodes().length; i++) {
        this.ChildRecursion(parent.getChildTransformNodes()[i], child_name);
      }
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 3)) {
      return false;
    } else {
      // 参数1：物体
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._parentObj = para1.gameObject;

      // 参数2：物体
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._childStr = para2.string;

      // 参数1：物体
      let para3: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para3, para_array[2])) {
        return false;
      }
      this._blueObj = para3.gameObject;

      return true;
    }
  }

  public destroy(): void {

  }
}

export class Action_Get_GameObject_Distance extends VE_ActionBehaviour {

  public get ID(): string {
    return "Get_GameObject_Distance|计算物体距离";
  }

  public get className(): string {
    return "Action_Get_GameObject_Distance";
  }

  private _blueObj: VeryGameObject = new VeryGameObject();
  private _blueObj2: VeryGameObject = new VeryGameObject();
  private _blueFloat: VeryNumber = new VeryNumber();


  public active(): void {
    var tmpNode1 = this._blueObj.value?.transform.transformNode;
    var tmpNode2 = this._blueObj2.value?.transform.transformNode;
    if (tmpNode1 != null && tmpNode1 != undefined && tmpNode2 != null && tmpNode2 != undefined) {
      this._blueFloat.value = BABYLON.Vector3.Distance(tmpNode1.getAbsolutePosition(), tmpNode2.getAbsolutePosition());
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 3)) {
      return false;
    } else {
      // 参数1：物体
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._blueObj = para1.gameObject;

      // 参数2：物体
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._blueObj2 = para2.gameObject;

      // 参数3：接收距离变量
      let para3: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para3, para_array[2])) {
        return false;
      }
      this._blueFloat = para3.number;

      return true;
    }
  }

  public destroy(): void {

  }
}

