export class Action_Set_Layer extends VE_ActionBehaviour {

  public get ID(): string {
    return "Set_Layer|设置层";
  }

  public get className(): string {
    return "Action_Set_Layer";
  }

  private _blueObj: VeryGameObject = new VeryGameObject();
  private _blueInt: VeryNumber = new VeryNumber();
  private _setChild: VeryBool = new VeryBool();


  public active(): void {
    if (this._blueObj.value?.mesh != null) {
      if (this._setChild.value) {
        this.Recursion(this._blueObj.value.mesh, this._blueInt.value);
      }
      else {
        this._blueObj.value.mesh.layerMask = this._blueInt.value;
      }
    }
  }

  private Recursion(obj: BABYLON.AbstractMesh, layer: number) {
    obj.layerMask = layer;
    if (obj.getChildMeshes().length > 0) {
      for (var i = 0; i < obj.getChildMeshes().length; i++) {
        this.Recursion(obj.getChildMeshes()[i], layer);
      }
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2, 3)) {
      return false;
    } else {
      // 参数1：物体
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._blueObj = para1.gameObject;

      // 参数2：层级
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._blueInt = para2.number;

      //参数3：是否设置子物体
      if (para_array.length > 2) {
        let para3: VeryParas = new VeryParas();
        if (!ParaParserUtility.BoolParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para3, para_array[2])) {
          return false;
        }
        this._setChild = para3.bool;
      }

      return true;
    }
  }

  public destroy(): void {

  }
}

