export class Action_Set_Parent extends VE_ActionBehaviour {

  public get ID(): string {
    return 'Set_Parent|设置父物体';
  }

  public get className(): string {
    return 'Action_Set_Parent';
  }

  private _childList: VeryGameObject[] = [];
  private _formerList: BABYLON.Nullable<Transform>[] = [];
  private _parentObj: VeryGameObject = new VeryGameObject();

  private _reset: VeryBool = new VeryBool();

  public active(): void {
  }

  public onUpdate(): void {
    if (this._reset.value) {
      for (var i = 0; i < this._childList.length; i++) {
        var tmpOBJ = this._childList[i].value;
        if (this._parentObj.value == null) {
          if (tmpOBJ != null)
            tmpOBJ.transform.parent = null;
        }
        else {
          if (tmpOBJ != null)
            tmpOBJ.transform.parent = this._parentObj.value.transform;
        }
      }
    }
    else {
      for (var i = 0; i < this._childList.length; i++) {
        var tmpOBJ = this._childList[i].value;
        if (tmpOBJ != null)
          tmpOBJ.transform.parent = this._formerList[i];
      }
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2, 3)) {
      return false;
    } else {
      // 参数1：
      var isRight = true;
      var nameList: string[] = para_array[0].split(/,|，/);
      for (var i = 0; i < nameList.length; i++) {
        nameList[i] = nameList[i].trim();
        var newObj: VeryGameObject = new VeryGameObject();
        let para1: VeryParas = new VeryParas();
        if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, nameList[i])) {
          return false;
        }
        newObj = para1.gameObject;
        if (newObj.value == null) {
          console.error(this.ID + " 响应中，物体" + nameList[i] + "在场景中不存在！");
          isRight = false;
          continue;
        }
        this._childList[this._childList.length] = (newObj);
        this._formerList[this._formerList.length] = (newObj.value.transform.parent);
      }
      if (!isRight) {
        console.error(this.ID + " 响应传入参数格式错误： " + para_array[0] + "，场景中有物体不存在，或者变量填写错误，请检查！");
        return false;
      }

      // 参数2：
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._parentObj = para2.gameObject;

      // 参数3：
      let para3: VeryParas = new VeryParas();
      if (para_array.length > 2) {
        if (!ParaParserUtility.BoolParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para3, para_array[2])) {
          return false;
        }
        this._reset = para3.bool;
      }
      else {
        this._reset.value = true;
      }



      return true;
    }
  }

  public destroy(): void {
    // 
  }

}

