export class Action_Get_Parent extends VE_ActionBehaviour {

  public get ID(): string {
    return 'Get_Parent|获取父物体';
  }

  public get className(): string {
    return 'Action_Get_Parent';
  }

  private _obj: VeryGameObject = new VeryGameObject();
  private _parent: VeryGameObject = new VeryGameObject();

  public active(): void {
  }

  public onUpdate(): void {
    if (this._obj.value != null) {
      if (this._obj.value.transform.parent == null) {
        this._parent.value = null;
      }
      else {
        //TODO:transform应该有一个gameobject属性
        this._parent.value = GameObject.Find(this._obj.value.transform.parent.name);
      }
    }
    else {
      this._parent.value = null;
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2)) {
      return false;
    } else {
      // 参数1：
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._obj = para1.gameObject;

      // 参数2：
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
        return false;
      }
      this._parent = para2.gameObject;

      return true;
    }
  }

  public destroy(): void {
    // 
  }

}

