import { VE_ActionBehaviour, VE_Actions } from "../../action";
import { ParaParserUtility, VeryParas, ErrorInfo } from "../../utility";
import { ParaType } from "../../enum";
import { VeryNumber, VeryString, IVeryVar, VeryBool, VarTools } from "../../variables";

export class Action_Active_Camera extends VE_ActionBehaviour {

  public get ID(): string {
    return "Active_Camera|激活相机";
  }

  public get className(): string {
    return "Action_Active_Camera";
  }

  private _carema1: BABYLON.Nullable<BABYLON.Camera> = null;
  private _cameraList: BABYLON.Camera[] = [];

  public active(): void {
    if (this._carema1) {
      this._carema1.setEnabled(true);
      for (var i = 0; i < this._cameraList.length; i++) {
        this._cameraList[i].setEnabled(false);
      }
    }
  }

  public onUpdate(): void {

  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 1, 2)) {
      return false;
    } else {
      // 参数1：
      this._carema1 = Camera_Tools.GetCamera(this.scene, para_array[0], this.ID);

      // 参数2：
      if (para_array.length > 1) {
        if (para_array[1].toLowerCase() == "null" || para_array[1].toLowerCase() == "none") {

        }
        else {
          var strings = para_array[1].split(/,|，/);
          for (var i = 0; i < strings.length; i++) {
            strings[i] = strings[i].trim();
            var tmpCamera = Camera_Tools.GetCamera(this.scene, strings[i], this.ID);
            if (tmpCamera != null)
              this._cameraList[this._cameraList.length] = (tmpCamera);
          }
        }
      }

      return true;
    }
  }
  public destroy(): void {

  }
}

export class Action_Set_Main_Camera extends VE_ActionBehaviour {

  public get ID(): string {
    return "Set_Main_Camera|设置主相机";
  }

  public get className(): string {
    return "Action_Set_Main_Camera";
  }

  private _carema1: BABYLON.Nullable<BABYLON.Camera> = null;

  public active(): void {
    if (this._carema1) {
      this.scene.activeCamera = this._carema1;
    }
  }

  public onUpdate(): void {

  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 1)) {
      return false;
    } else {
      // 参数1：
      this._carema1 = Camera_Tools.GetCamera(this.scene, para_array[0], this.ID);

      return true;
    }
  }
  public destroy(): void {

  }
}

export class Action_Get_CameraSize extends VE_ActionBehaviour {

  public get ID(): string {
    return "Get_CameraSize|获取相机视窗大小";
  }

  public get className(): string {
    return "Action_Get_CameraSize";
  }

  private _carema1: BABYLON.Nullable<BABYLON.Camera> = null;
  private _size: VeryNumber = new VeryNumber();

  public active(): void {
    if (this._carema1) {
      this._size.value = this._carema1.fov;
    }
  }

  public onUpdate(): void {

  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2)) {
      return false;
    } else {
      // 参数1：
      this._carema1 = Camera_Tools.GetCamera(this.scene, para_array[0], this.ID);

      //参数2
      let para3: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Trigger, para3, para_array[1])) {
        return false;
      }
      this._size = para3.number;

      return true;
    }
  }
  public destroy(): void {

  }
}

