import { VE_ActionBehaviour, VE_Actions } from "../../action";
import { ParaParserUtility, VE_KeyboardMap, VeryParas } from '../../utility';
import { ParaType, Severity } from '../../enum';
import { VE_ErrorManager, VE_Error } from '../../global';
import { VeryGameObject, VeryBool, VeryNumber, VeryString, VeryRect, VeryVector3, VeryVector2, VeryMaterial } from "../../variables";

export class Action_Get_Material extends VE_ActionBehaviour {

  public get ID(): string {
    return 'Get_Material|获取材质球';
  }

  public get className(): string {
    return 'Action_Get_Material';
  }


  private _blueGameobject: VeryGameObject = new VeryGameObject();
  private _blueMaterial: VeryMaterial = new VeryMaterial();

  public active(): void {

  }

  public onUpdate(): void {
    var tmpMesh = this._blueGameobject.value?.mesh;
    if (tmpMesh != null && tmpMesh != undefined) {
      this._blueMaterial.value = tmpMesh.material;
    }
  }


  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Trigger, para_array.length, 2)) {
      return false;
    } else {
      // 第1个参数：
      let para0: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[0])) {
        return false;
      }
      this._blueGameobject = para0.gameObject;
      var tmpMesh = this._blueGameobject.value?.mesh;
      if (tmpMesh == null || tmpMesh == undefined) {
        console.error(this.ID + " 响应传入参数格式错误： " + para_array[0] + "，该物体不具有Mesh！");
        return false;
      }

      //参数2
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.MaterialParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[1])) {
        return false;
      }
      this._blueMaterial = para1.material;

      return true;
    }

  }
  public destroy(): void {

  }
}

export class Action_Set_Material extends VE_ActionBehaviour {

  public get ID(): string {
    return 'Set_Material|设置材质球';
  }

  public get className(): string {
    return 'Action_Set_Material';
  }


  private _blueGameobject: VeryGameObject = new VeryGameObject();
  private _blueMaterial: VeryMaterial = new VeryMaterial();

  public active(): void {

  }

  public onUpdate(): void {
    var tmpMesh = this._blueGameobject.value?.mesh;
    if (tmpMesh != null && tmpMesh != undefined) {
      tmpMesh.material = this._blueMaterial.value;
    }
  }


  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Trigger, para_array.length, 2)) {
      return false;
    } else {
      // 第1个参数：
      let para0: VeryParas = new VeryParas();
      if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[0])) {
        return false;
      }
      this._blueGameobject = para0.gameObject;
      var tmpMesh = this._blueGameobject.value?.mesh;
      if (tmpMesh == null || tmpMesh == undefined) {
        console.error(this.ID + " 响应传入参数格式错误： " + para_array[0] + "，该物体不具有Mesh！");
        return false;
      }

      //参数2
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.MaterialParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[1])) {
        return false;
      }
      this._blueMaterial = para1.material;

      return true;
    }

  }
  public destroy(): void {

  }
}

//TODO:20200807
VE_Actions.addAction(Action_Get_Material);
VE_Actions.addAction(Action_Set_Material);