import { VE_ActionBehaviour, VE_Actions } from "../../action";
import { ParaParserUtility, VE_KeyboardMap, VeryParas } from '../../utility';
import { ParaType, Severity } from '../../enum';
import { VE_ErrorManager, VE_Error } from '../../global';
import { VeryGameObject, VeryBool, VeryNumber, VeryString, VeryRect, VeryVector3, VeryVector2 } from "../../variables";

export class Action_Get_Rect_Value extends VE_ActionBehaviour {

  public get ID(): string {
    return 'Get_Rect_Value|获取矩形值';
  }

  public get className(): string {
    return 'Action_Get_Rect_Value';
  }

  private _isGet = [true, true, true, true];

  private _blueRect: VeryRect = new VeryRect();

  private _xFloat: VeryNumber = new VeryNumber();
  private _yFloat: VeryNumber = new VeryNumber();
  private _widthFloat: VeryNumber = new VeryNumber();
  private _heightFloat: VeryNumber = new VeryNumber();

  public active(): void {

  }

  public onUpdate(): void {
    if (this._isGet[0]) {
      this._xFloat.value = this._blueRect.value.x;
    }
    if (this._isGet[1]) {
      this._yFloat.value = this._blueRect.value.y;
    }
    if (this._isGet[2]) {
      this._widthFloat.value = this._blueRect.value.z;
    }
    if (this._isGet[3]) {
      this._heightFloat.value = this._blueRect.value.w;
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Trigger, para_array.length, 5)) {
      return false;
    } else {
      // 第1个参数：
      let para0: VeryParas = new VeryParas();
      if (!ParaParserUtility.RectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[0])) {
        return false;
      }
      this._blueRect = para0.rect;

      //参数2
      if (para_array[1].toLowerCase() == "none" || para_array[1].toLowerCase() == "null") {
        this._isGet[0] = false;
      }
      else {
        let para0: VeryParas = new VeryParas();
        if (!ParaParserUtility.NumberParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[1])) {
          return false;
        }
        this._xFloat = para0.number;
      }

      //参数3
      if (para_array[2].toLowerCase() == "none" || para_array[2].toLowerCase() == "null") {
        this._isGet[1] = false;
      }
      else {
        let para0: VeryParas = new VeryParas();
        if (!ParaParserUtility.NumberParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[2])) {
          return false;
        }
        this._yFloat = para0.number;
      }

      //参数4
      if (para_array[3].toLowerCase() == "none" || para_array[3].toLowerCase() == "null") {
        this._isGet[2] = false;
      }
      else {
        let para0: VeryParas = new VeryParas();
        if (!ParaParserUtility.NumberParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[3])) {
          return false;
        }
        this._widthFloat = para0.number;
      }

      //参数5
      if (para_array[4].toLowerCase() == "none" || para_array[4].toLowerCase() == "null") {
        this._isGet[3] = false;
      }
      else {
        let para0: VeryParas = new VeryParas();
        if (!ParaParserUtility.NumberParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[4])) {
          return false;
        }
        this._heightFloat = para0.number;
      }

      return true;
    }

  }

  public destroy(): void {

  }

}

export class Action_Rect_Contains extends VE_ActionBehaviour {

  public get ID(): string {
    return 'Rect_Contains|矩形包含';
  }

  public get className(): string {
    return 'Action_Rect_Contains';
  }

  //Rect
  private _isRect = false;
  private _blueRect: VeryRect = new VeryRect();
  private _rectFloat: VeryNumber[] = [];

  //点坐标
  private _isVec3 = false;
  private _blueVector3 = new VeryVector3();

  private _isVec2 = false;
  private _blueVector2 = new VeryVector2();

  private _blueX = new VeryNumber();
  private _blueY = new VeryNumber();
  private _blueZ = new VeryNumber();

  //是否包含bool值
  private _blueBool = new VeryBool();

  public active(): void {

  }

  public onUpdate(): void {
    var rect = this._blueRect.value;
    if (!this._isRect) {
      rect = new BABYLON.Vector4(this._rectFloat[0].value, this._rectFloat[1].value, this._rectFloat[2].value, this._rectFloat[3].value);
    }

    if (this._isVec3) {
      this._blueBool.value = this.RectContainsVector3(rect, this._blueVector3.value);
    }
    else if (this._isVec2) {
      this._blueBool.value = this.RectContainsVector2(rect, this._blueVector2.value);
    }
    else {
      var _setVector2 = new BABYLON.Vector2(this._blueX.value, this._blueY.value);
      this._blueBool.value = this.RectContainsVector2(rect, _setVector2);
    }
  }

  private RectContainsVector3(rect: BABYLON.Vector4, vec3: BABYLON.Vector3) {
    if (vec3.x >= rect.x && vec3.x <= (rect.x + rect.z) && vec3.y >= rect.y && vec3.y <= (rect.y + rect.w)) {
      return true;
    }
    return false;
  }
  private RectContainsVector2(rect: BABYLON.Vector4, vec2: BABYLON.Vector2) {
    if (vec2.x >= rect.x && vec2.x <= (rect.x + rect.z) && vec2.y >= rect.y && vec2.y <= (rect.y + rect.w)) {
      return true;
    }
    return false;
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Trigger, para_array.length, 3)) {
      return false;
    } else {
      // 第1个参数：
      let para0: VeryParas = new VeryParas();
      if (!ParaParserUtility.RectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[0])) {
        return false;
      }
      this._isRect = para0.isRect;
      this._blueRect = para0.rect;
      this._rectFloat = para0.rectNumbers;

      //参数2
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.Vector3OrNumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[1])) {
        if (!ParaParserUtility.Vector2OrNumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[1])) {
          return false;
        }
        else {
          this._isVec2 = para1.isVec2;
          this._blueVector2 = para1.vector2;
          this._blueX = para1.vec2Numbers[0];
          this._blueY = para1.vec2Numbers[1];
        }
      }
      else {
        this._isVec3 = para1.isVec;
        this._blueVector3 = para1.vector3;
        this._blueX = para1.vecNumbers[0];
        this._blueY = para1.vecNumbers[1];
        this._blueZ = para1.vecNumbers[2];
      }

      //参数3
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.BoolParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[2])) {
        return false;
      }
      this._blueBool = para2.bool;

      return true;
    }
  }

  public destroy(): void {

  }

}

export class Action_Set_Rect_Value extends VE_ActionBehaviour {

  public get ID(): string {
    return 'Set_Rect_Value|设置矩形值';
  }

  public get className(): string {
    return 'Action_Set_Rect_Value';
  }

  private _isRect = false;
  private _blueFloatArray: VeryNumber[] = [];
  private _blueRect = new VeryRect();

  private _AcceptRect = new VeryRect();

  public active(): void {

  }

  public onUpdate(): void {
    if (!this._isRect) {
      this._AcceptRect.value = new BABYLON.Vector4(this._blueFloatArray[0].value, this._blueFloatArray[1].value, this._blueFloatArray[2].value, this._blueFloatArray[3].value);
    }
    else {
      this._AcceptRect.value = this._blueRect.value;
    }
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Trigger, para_array.length, 2)) {
      return false;
    } else {
      // 第1个参数：
      let para0: VeryParas = new VeryParas();
      if (!ParaParserUtility.RectOrNumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para0, para_array[0])) {
        return false;
      }
      this._isRect = para0.isRect;
      this._blueRect = para0.rect;
      this._blueFloatArray = para0.rectNumbers;

      //参数2
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.RectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[1])) {
        return false;
      }
      this._AcceptRect = para1.rect;

      return true;
    }

  }
  public destroy(): void {

  }
}

//TODO:20200807
VE_Actions.addAction(Action_Get_Rect_Value);
VE_Actions.addAction(Action_Rect_Contains);
VE_Actions.addAction(Action_Set_Rect_Value);