export class Trigger_Bool_All_True extends VE_TriggerBehaviour {

  public get ID(): string {
    return 'Bool_All_True|布尔值全为真 ';
  };

  public get className(): string {
    return 'Trigger_Bool_All_True';
  };

  private _resultBool: VeryBool = new VeryBool();
  private _boolList: VeryBool[] = [];

  private _observer: Nullable<BABYLON.Observer<BABYLON.Scene>> = null;


  public paraParser(para_array: string[]): boolean {

    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Trigger, para_array.length, 2)) {
      return false;
    }
    else {

      this._boolList = [];
      var strs: string[] = para_array[0].split(/,|，/);
      for (var i = 0; i < strs.length; i++) {
        strs[i] = strs[i].trim();
        var tempBool: VeryBool = new VeryBool();
        let para0: VeryParas = new VeryParas();
        if (!ParaParserUtility.BoolParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Trigger, para0, strs[i])) {
          return false;
        }
        tempBool = para0.bool;
        this._boolList[this._boolList.length] = tempBool;
      }

      if (para_array[1].toLowerCase() == "none" || para_array[1].toLowerCase() == "null") {

      }
      else {
        let para1: VeryParas = new VeryParas();
        if (!ParaParserUtility.BoolParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Trigger, para1, para_array[1])) {
          return false;
        }
        this._resultBool = para1.bool;
      }

      this._observer = this.scene.onBeforeRenderObservable.add(() => {
        this._resultBool.value = true;
        for (var i = 0; i < this._boolList.length; i++) {
          if (this._boolList[i].value == false) {
            this._resultBool.value = false;
            break;
          }
        }
        if (this._resultBool.value == true) {
          this.sendEvent();
        }
      });

      return true;
    }
  }

  public destroy(): void {
    if (this._observer) {
      this._scene.onBeforeRenderObservable.remove(this._observer);
    }
    this._observer = null;
  }
}

