export class Trigger_Number_Compare extends VE_TriggerBehaviour {

  public get ID(): string {
    return 'Number_Compare|数值比较 ';
  };

  public get className(): string {
    return 'Trigger_Number_Compare';
  };

  private _veryNumber1: VeryNumber = new VeryNumber();
  private _veryNumber2: VeryNumber = new VeryNumber();
  private _accuracy: VeryNumber = new VeryNumber();
  private _operatorType: string = "";

  private _observer: Nullable<BABYLON.Observer<BABYLON.Scene>> = null;

  public paraParser(para_array: string[]): boolean {

    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Trigger, para_array.length, 4)) {
      return false;
    }
    else {
      //第一个参数
      let para0: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberPara(this.projectName, this.objectID, this.ID, ParaType.Trigger, para0, para_array[0])) {
        return false;
      }
      this._veryNumber1 = para0.number;

      //第2个参数
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberPara(this.projectName, this.objectID, this.ID, ParaType.Trigger, para1, para_array[1])) {
        return false;
      }
      this._veryNumber2 = para1.number;

      //第3个参数
      let para2: VeryParas = new VeryParas();
      if (!ParaParserUtility.NumberPara(this.projectName, this.objectID, this.ID, ParaType.Trigger, para2, para_array[2])) {
        return false;
      }
      this._accuracy = para2.number;

      //第4个参数
      this._operatorType = para_array[3];
      if (this._operatorType != "==" && this._operatorType != "=" && this._operatorType != ">" && this._operatorType != "<" && this._operatorType != ">=" && this._operatorType != "<=") {
        console.error(this.ID + " 触发传入参数格式错误： + " + para_array[3] + "，应为 “=”、“>”、“>=”、“<”或者“<=”运算符，请检查！");
        return false;
      }

      this._observer = this.scene.onBeforeRenderObservable.add(() => {
        switch (this._operatorType) {
          case "==":
            if (Math.abs(this._veryNumber1.value - this._veryNumber2.value) < this._accuracy.value) {
              this.sendEvent();
            }
            break;
          case "=":
            if (Math.abs(this._veryNumber1.value - this._veryNumber2.value) < this._accuracy.value) {
              this.sendEvent();
            }
            break;
          case ">":
            if (this._veryNumber1.value > this._veryNumber2.value) {
              this.sendEvent();
            }
            break;
          case ">=":
            if (this._veryNumber1.value >= this._veryNumber2.value) {
              this.sendEvent();
            }
            break;
          case "<":
            if (this._veryNumber1.value < this._veryNumber2.value) {
              this.sendEvent();
            }
            break;
          case "<=":
            if (this._veryNumber1.value <= this._veryNumber2.value) {
              this.sendEvent();
            }
            break;
          default: break;
        }
      });

      return true;
    }
  }

  public destroy(): void {
    if (this._observer) {
      this._scene.onBeforeRenderObservable.remove(this._observer);
    }
    this._observer = null;
  }
}

