export class Trigger_Number_Multi_Compare extends VE_TriggerBehaviour {

  public get ID(): string {
    return 'Number_Multi_Compare|数值多重比较 ';
  };

  public get className(): string {
    return 'Trigger_Number_Multi_Compare';
  };


  private _boolOperator: string = "";  //布尔运算符号
  private _floatList: VeryNumber[] = [];
  private _operatorList: string[] = [];
  private _resultList: boolean[] = [];

  private _observer: Nullable<BABYLON.Observer<BABYLON.Scene>> = null;

  public paraParser(para_array: string[]): boolean {

    if (para_array.length < 2) {
      return false;
    }
    else {
      //第1个参数
      if (para_array[0] == "&&" || para_array[0].toLowerCase() == "and" || para_array[0] == "与") {
        this._boolOperator = "&&";
      }
      else if (para_array[0] == "||" || para_array[0].toLowerCase() == "or" || para_array[0] == "或") {
        this._boolOperator = "||";
      }
      else {
        console.error(this.ID + " 触发参数格式错误，当前应传入“&&”、“and”或“与”表示“与计算”， “||”、“or”或“或”表示“或运算”，当前传入值为：" + para_array[0] + "！");
        return false;
      }

      //第2个及2个以上的参数解析
      for (var i = 1; i < para_array.length; i++) {
        var floatArray: string[] = para_array[i].split(/,|，/);
        if (floatArray.length != 4) {
          console.error(this.ID + " 触发参数格式错误，一组浮点数运算应包含4个参数，当前传入值为：" + para_array[i] + "，不符合，请检查！");
          return false;
        }
        //操作数
        for (var k = 0; k < 3; k++) {
          floatArray[k] = floatArray[k].trim();
          let para1: VeryParas = new VeryParas();
          if (!ParaParserUtility.NumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Trigger, para1, floatArray[k])) {
            return false;
          }
          this._floatList[this._floatList.length] = para1.number;
        }
        //操作符
        floatArray[3] = floatArray[3].trim();
        if (floatArray[3] != "==" && floatArray[3] != "=" && floatArray[3] != ">" && floatArray[3] != "<" && floatArray[3] != ">=" && floatArray[3] != "<=") {
          console.error(this.ID + " 触发传入参数格式错误： + " + para_array[3] + "，应为 “=”、“>”、“>=”、“<”或者“<=”运算符，请检查！");
          return false;
        }
        this._operatorList[this._operatorList.length] = (floatArray[3]);
        this._resultList[this._resultList.length] = (true);
      }

      this._observer = this.scene.onBeforeRenderObservable.add(() => {
        for (var i = 0; i < this._operatorList.length; i++) {
          if (this._operatorList[i] == "=" || this._operatorList[i] == "==") {
            if (Math.abs(this._floatList[i * 3].value - this._floatList[i * 3 + 1].value) < this._floatList[i * 3 + 2].value) {
              this._resultList[i] = true;
            }
            else {
              this._resultList[i] = false;
            }
          }
          else if (this._operatorList[i] == ">") {
            if (this._floatList[i * 3].value > this._floatList[i * 3 + 1].value) {
              this._resultList[i] = true;
            }
            else {
              this._resultList[i] = false;
            }
          }
          else if (this._operatorList[i] == ">=") {
            if (this._floatList[i * 3].value >= this._floatList[i * 3 + 1].value) {
              this._resultList[i] = true;
            }
            else {
              this._resultList[i] = false;
            }
          }
          else if (this._operatorList[i] == "<") {
            if (this._floatList[i * 3].value < this._floatList[i * 3 + 1].value) {
              this._resultList[i] = true;
            }
            else {
              this._resultList[i] = false;
            }
          }
          else if (this._operatorList[i] == "<=") {
            if (this._floatList[i * 3].value <= this._floatList[i * 3 + 1].value) {
              this._resultList[i] = true;
            }
            else {
              this._resultList[i] = false;
            }
          }
        }
        var result: boolean = false;
        if (this._resultList.length == 1) {
          result = this._resultList[0];
        }
        else {
          result = this._resultList[0];
          for (var i = 1; i < this._resultList.length; i++) {
            if (this._boolOperator == "&&") {
              result = result && this._resultList[i];
            }
            else {
              result = result || this._resultList[i];
            }
          }
        }
        if (result) {
          this.sendEvent();
        }
      });

      return true;
    }
  }

  public destroy(): void {
    if (this._observer) {
      this._scene.onBeforeRenderObservable.remove(this._observer);
    }
    this._observer = null;
  }
}

